function Home() {

    const [open, setOpen] = useState(false)
    return (
        <Container>
            <header>
                <Banner image={bannerHome} />

                <div className="box-center">
                    <div className="container-menu">
                        {open ? <FiX className="icon-menu" onClick={() => setOpen(false)} /> : <FiMenu className="icon-menu" onClick={() => setOpen(true)} />}
                    </div>

                    <img className="logo-ailleclinic" src={logo} alt="Logo Aille Clinic" />

                    <h2 className="subtitle">role a página e conheça</h2>
                </div>
            </header>

            <main>
                {dataText.map((text) =>
                    <CardText
                        key={text.title}
                        image={text.image}
                        title={text.title}
                        type={text.type}
                        menssage={text.menssage}
                    />
                )}

                <div className="cont-prof">
                    {dataProfi.map((prof) =>
                        <CardProf
                            key={prof.id}
                            name={prof.name}
                            image={prof.image}
                            profession={prof.profession}
                            description={prof.description}
                            linkedin={prof.linkedin}
                            instagram={prof.instagram}
                        />
                    )}
                </div>

                {dataInfo.map((info) =>
                    <CardInfo
                        key={info.id}
                        type={info.type}
                        image={info.image}
                        title={info.title}
                        description={info.description}
                        buttonLabel={info.buttonLabel}
                        buttonHref={info.buttonHref}
                    />
                )}

            </main>

            <Footer />

        </Container>
    );
}

export default Home;