import styled from "styled-components";

export const Container = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    max-width: 600px;
    margin-top: 20px;

    .image1 {
        margin-top: 38px;
        width: 100%;
    }

    .title {
        margin: 38px 0;
        font-style: var(--unnamed-font-style-normal);
        font-weight: var(--unnamed-font-weight-600);
        font-size: var(--unnamed-font-size-18);
        line-height: var(--unnamed-font-size-20);
        letter-spacing: var(--unnamed-character-spacing-1);
        color: var(--unnamed-color-d5b68f);
        text-transform: uppercase;
    }

    p {
        font-weight: var(--unnamed-font-style-normal);
        font-size: var(--unnamed-font-size-14);
        line-height: var(--unnamed-line-spacing-24);
        letter-spacing: var(--unnamed-character-spacing-0);
        color: var(--unnamed-color-827566);
        margin: 0;
    }

    p + p {
        margin-top: 25px;
    }

    @media (min-width: 800px) {
        ${(p) => p.type === 1 ? `
            flex-direction: row-reverse;
            justify-content: space-between;
            max-width: 1600px;
            margin-top: 20px;

            .image1 {
                margin-top: 0px;
                width: 55%;
                height: 35vw;
                max-height: 550px;
            }

            .container-desk-text {
                width: 40%;
                display: flex;
                flex-direction: column;
                align-items: center;

                .box-info-tam {
                    max-width: 300px;
                    margin-top: 130px;
                }
            }
        ` : p.type === 2 ? `
            flex-direction: row;
            justify-content: space-between;
            max-width: 1600px;
            margin-top: 90px;
            margin-bottom: 60px;

            .image1 {
                margin-top: 0px;
                width: 55%;
                height: 35vw;
                max-height: 550px;
            }

            .container-desk-text {
                width: 40%;
                display: flex;
                flex-direction: column;
                align-items: center;

                .box-info-tam {
                    max-width: 360px;
                    margin-top: 0px;
                }
            }
        ` : null}
    }
`;